import React, { useState, useEffect, } from 'react';
import { Row, Col, Card } from 'antd';
import { TR, } from '../../lang/types';
import { tr, en } from '../lang';

const LanguageTranslatorV3 = require('ibm-watson/language-translator/v3');
const { IamAuthenticator } = require('ibm-watson/auth');

const apiKey = process.env.REACT_APP_TRANSLATE_API_KEY;
const serviceURL = process.env.REACT_APP_TRANSLATE_URL;

const { Meta } = Card;

const ItemContainer = ({ Title, imdbID, Poster, Year, ShowDetail, DetailRequest, ActivateModal, API_KEY, Lang }) => {

    const [text, setText] = useState(en);

    useEffect(() => {
        if (Lang === TR) {
            setText(tr);
        }
        else { setText(en); }
    }, [Lang,]);

    const clickHandler = () => {

        ActivateModal(true);
        DetailRequest(true);
        fetch(`https://www.omdbapi.com/?i=${imdbID}&apikey=${API_KEY}`)
            .then(resp => resp)
            .then(resp => resp.json())
            .then(response => {
                //console.log(apiKey + "\n" + serviceURL);

                if (Lang === TR) {
                    setText(tr);
                    const languageTranslator = new LanguageTranslatorV3({
                        version: '2018-05-01',
                        authenticator: new IamAuthenticator({
                            apikey: apiKey,
                        }),
                        serviceUrl: serviceURL,
                    });

                    const translateParams = {
                        text: response.Plot,
                        modelId: 'en-tr',
                    };

                    languageTranslator.translate(translateParams)
                        .then(translationResult => {
                            let result = JSON.stringify(translationResult, null, 2).translations.translation;
                            response.Plot = result;
                        })
                        .catch(err => {
                            console.log('error:', err);

                        });
                }
                DetailRequest(false);
                ShowDetail(response);

            })
            .catch(({ message }) => {
                DetailRequest(false);
            })
    }
    return (
        <Col style={{ margin: '20px 0' }} className="gutter-row" span={4}>
            <div className="gutter-box">
                <Card
                    style={{ width: 200 }}
                    cover={
                        <img
                            alt={Title}
                            src={Poster === 'N/A' ? require('../images/ImageNotFound.png') : Poster}
                        />
                    }
                    onClick={() => clickHandler()}
                >
                    <Meta
                        title={Title}
                        description={false}
                    />
                    <Row style={{ marginTop: '10px', justifyContent: 'center' }} className="gutter-row">
                        <Col>
                            <div> {text.year}: {Year}</div>
                        </Col>
                        <Col>
                            <div>IMDb ID: {imdbID} </div>
                        </Col>
                    </Row>
                </Card>
            </div>
        </Col>
    )
};
export { ItemContainer };